@import url(https://fonts.googleapis.com/css?family=Oxygen);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
:root {
  --primary-font-color: #43a047;
  --default-font-color: #212529;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Oxygen', sans-serif;
  color: #212529;
  color: var(--default-font-color);
  font-size: 0.9em;
  width: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  padding-bottom: 20px;
}
button:focus {
  outline: none;
}

.svg-button {
  width: 20px;
  height: 20px;
  color: red;
}

a {
  color: #43a047;
  color: var(--primary-font-color);
}
a:hover {
  color: #212529;
  color: var(--default-font-color);
  text-decoration: none;
}
.custom-table {
  overflow-x: scroll;
}

.top-content {
  height: 80%;
  background: url(/static/media/background.c3a398cf.jpg);
}
.bottom-content {
  margin-top: -100px;
  background: white;
}
.home-text {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 40px;
}
.how-it-works {
  background-color: #fafafa;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation {
  margin-bottom: 0px;
  display: -webkit-flex;
  display: flex;
  height: 60px;
  padding: 8px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.brand {
  -webkit-flex: 1 1;
          flex: 1 1;
}
.links li {
  display: inline;
  margin-right: 24px;
}
.links li:last-child {
  margin-right: 0px;
}
.link,
.a {
  color: var(--default-font-color);
}
.a {
  color: var(--primary-font-color);
}
.link {
  font-weight: bold;
}
.link:hover,
.a:hover {
  text-decoration: none;
  color: var(--primary-font-color);
}
.a:hover {
  color: var(--default-font-color);
}

.dashboard-navigation {
  margin-bottom: 0px;
}
.dashboard-navigation li {
  margin-right: 30px;
  list-style: none;
  border-bottom: 2px solid transparent;
}
.active-link {
  border-bottom-color: var(--primary-font-color) !important;
}
.dashboard-navigation li:last-child {
  margin-right: 0px;
}
.dashboard-navigation a {
  display: block;

  padding-bottom: 10px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  color: var(--default-font-color);
}
.dashboard-navigation a:hover {
  color: var(--primary-font-color);
}

